* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navlist {
  height: 10vh;
  display: flex;
  width: 50%;
  justify-content: space-around;
  align-items: center;
  margin-left: auto;
  list-style:none;

    .navlinks {
      text-decoration: none;
      color: #555;
      font-size: 18px;
    }
}

.home,
.about,
.contact {
  height: 90vh;
  position: absolute;
  width: 100%;

    .home-content,
    .about-content,
    .contact-content {
      height: 100%;
      display: flex;
      justify-content: space-evenly;
      align-items: center;

       img {
         height: 70%;
       }

       h1 {
         font-size: 50px;
         margin: 20px 0;
       }

       p {
         font-size: 26px;
       }
    }
}

.home {
  background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}

.about {
  background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}

.contact {
  background-image: linear-gradient(to top, #30cfd0 0%, #330867 100%);
}
